* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  backdrop-filter: blur(20px);
  text-align: center;
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 20px;
  margin-bottom: 30px;
  padding: 30px;
}

.header h1 {
  color: #fff;
  text-shadow: 2px 2px 4px #0000004d;
  margin-bottom: 10px;
  font-size: 2.5em;
}

.header p {
  color: #fffc;
  font-size: 1.2em;
}

.connection-status {
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  padding: 10px 20px;
  font-weight: bold;
  transition: all .3s;
  display: flex;
}

.connection-status.connected {
  color: #22c55e;
  background: #22c55e33;
  border: 1px solid #22c55e4d;
}

.connection-status.disconnected {
  color: #ef4444;
  background: #ef444433;
  border: 1px solid #ef44444d;
}

.connection-status.connecting {
  color: #fbbf24;
  background: #fbbf2433;
  border: 1px solid #fbbf244d;
}

.controls {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
  display: flex;
}

.control-button {
  backdrop-filter: blur(10px);
  color: #fff;
  cursor: pointer;
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 25px;
  min-width: 120px;
  padding: 12px 24px;
  font-size: 1em;
  font-weight: 500;
  transition: all .3s;
}

.control-button:hover {
  background: #fff3;
  transform: translateY(-2px);
  box-shadow: 0 10px 20px #0000001a;
}

.control-button:active {
  transform: translateY(0);
}

.market-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
  display: grid;
}

.market-card {
  backdrop-filter: blur(20px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 20px;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.market-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px #0003;
}

.market-card:before {
  content: "";
  opacity: 0;
  background: linear-gradient(90deg, #00f5ff, #0080ff);
  height: 3px;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.market-card.updated:before {
  opacity: 1;
}

.symbol {
  color: #fff;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  font-size: 1.4em;
  font-weight: bold;
  display: flex;
}

.price {
  color: #fff;
  margin-bottom: 5px;
  font-size: 2em;
  font-weight: bold;
}

.change {
  align-items: center;
  gap: 5px;
  margin-bottom: 10px;
  font-size: 1.1em;
  font-weight: 500;
  display: flex;
}

.change.positive {
  color: #22c55e;
}

.change.negative {
  color: #ef4444;
}

.change.neutral {
  color: #94a3b8;
}

.market-details {
  border-top: 1px solid #ffffff1a;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin-top: 15px;
  padding-top: 15px;
  display: grid;
}

.detail-item {
  flex-direction: column;
  gap: 2px;
  display: flex;
}

.detail-label {
  color: #fff9;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .8em;
}

.detail-value {
  color: #fff;
  font-size: 1em;
  font-weight: 500;
}

.timestamp {
  color: #ffffff80;
  margin-top: 10px;
  font-size: .75em;
}

.stats-panel {
  backdrop-filter: blur(20px);
  text-align: center;
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 20px;
}

.stats-panel h3 {
  color: #fff;
  margin-bottom: 20px;
  font-size: 1.3em;
}

.stats-grid {
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 15px;
  display: grid;
}

.stat-item {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.stat-value {
  color: #fff;
  font-size: 1.5em;
  font-weight: bold;
}

.stat-label {
  color: #ffffffb3;
  font-size: .9em;
}

.loading {
  text-align: center;
  color: #ffffffb3;
  padding: 40px;
}

.spinner {
  border: 4px solid #ffffff4d;
  border-top-color: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: 0 auto 20px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .7;
  }
}

.pulse {
  animation: 1s ease-in-out pulse;
}

@media (width <= 768px) {
  .header h1 {
    font-size: 2em;
  }

  .controls {
    flex-direction: column;
    align-items: center;
  }

  .market-grid {
    grid-template-columns: 1fr;
  }
}
